body {
  margin: 0;
}

li {
  list-style: none;
}

h2, p, ul {
  margin: 0;
  padding: 0;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.team {
  background-color: rgba(165, 158, 158, .54);
  margin-top: 400px;
  margin-bottom: 200px;
}

.team-card {
  width: 500px;
  height: 550px;
  scroll-snap-type: x mandatory;
  background: #4c4c50;
  border-radius: 8px;
  gap: 12px;
  margin-left: auto;
  margin-right: auto;
  padding: 24px;
  scroll-padding: 24px;
  display: flex;
  overflow-x: scroll;
  box-shadow: 0 8px 30px rgba(66, 60, 62, .1);
}

.team-title {
  text-align: center;
  color: #fff;
}

.team-box {
  width: 350px;
  padding-left: 75px;
}

.team-text {
  scroll-snap-align: start;
  color: #fff;
  border-radius: 8px;
  flex: 0 0 100%;
  padding-left: 0;
  padding-right: 24px;
}

